.hero {
  background-color: $gray;
  background-size: cover, cover, 300px, cover;
  background-position: center;
  padding: 3rem 0;
  @include tablet {
    padding: 6rem 0;
  }
  text-align: center; 
  color: $white;
  text-shadow: 2px 2px 3px rgba(black, 0.8);
  *[class*="btn"] {
    text-shadow: none;
  }
  h2 {
    text-transform: none;
    font-weight: normal;
    font-size: 1.4em;
    line-height: 1.3;
    @include bp(md) {
      max-width: 75%;
      margin: 0 auto;
    }
  }
  [class*='color-box-subheadline'] {
    font-size: 1.25em;
  }
}

.home .hero {
  padding: 0;
  .slide {
    background-size: cover;
    padding: 3rem 0 6rem;
    @include tablet {
      padding: 6rem 0 10rem;
    }
  }
  h2 {
    margin-bottom: 1em;
  }
}

.single-post .hero {
  padding: 3rem 0;
  @include bp(md) {
    padding: 6rem 0;
  }
  h2 {
    @include bp(sm) {
      font-size: 1.2em;
    }
  }
  p {
    margin-bottom: 0;
  }
}